import  { api, isArray} from '../util/index'
import { createEle } from './index'

function sameNode(oldVnode, vnode){
	return vnode.tagName === oldVnode.tagName;
}

function patchNode(oldVnode, vnode){
	const el = vnode.el = oldVnode.el;
    let i, oldCh = oldVnode.children, ch = vnode.children;
    if (oldVnode === vnode) return;
    //update class attr	
    api.setClass(el, vnode.className);
    api.setAttrs(el, vnode.data);
    if(oldCh && ch && oldCh !== ch){
    	updateChildren(el, oldCh, ch);
    }else if(ch){
    	createEle(vnode); //create el children
    }else if(oldCh){
    	api.removeChildren(el);
    }
}

function updateChildren(oldch, ch){

}

export function patch(oldVnode, vnode){
	
	if(sameNode(oldVnode, vnode)){
		patchNode(oldVnode, vnode);
	}else{
		const oEl = oldVnode.el;
		let parentEle = api.parentNode(oEl);
		createEle(vnode);
		if(parentEle !== null){
			api.insertBefore(parentEle, vnode.el, api.nextSibling(oEl));
			api.removeChild(parentEle, oldVnode.el);
			oldVnode = null;
		}
	}
	return vnode;
}


























